<template>
  <div class="">
    <div class="home-content" v-if="selectedSurah == null">
      <div class="search-box">
        <input
          type="text"
          class="filter-surah"
          placeholder="Cari surah disini..."
          v-model="keyword"
        />
      </div>
      <div class="all-surah">
        <div
          class="surah-info"
          v-for="(item, index) in filteredSurah"
          :key="index"
          @click="gotoSurah(item)"
        >
          <div class="no-surah">{{ item.nomor }}</div>
          <div class="nama-surah">{{ item.nama }}</div>
        </div>
      </div>
    </div>

    <div class="surah-content" v-if="selectedSurah !== null">
      <div class="navigation">
        <div class="prev">Surah Sebelumnya</div>
        <div class="home" @click="resetSurah">Surah Lain</div>
        <div class="next">Surah Selanjutnya</div>
      </div>
      <div class="title">
        {{ selectedSurah.nama }} - {{ selectedSurah.asma }}
      </div>
      <div class="info">
        <div class="total-ayat">Total Ayat: {{ selectedSurah.ayat }}</div>
        <div class="tipe">Tipe Surat: {{ selectedSurah.type }}</div>
        <div class="keterangan" v-html="selectedSurah.keterangan"></div>
      </div>
      <div class="isi" v-for="(item, index) in selectedSurah.isi" :key="index">
        <div class="utama">
          <div class="kiri">
            <div class="nomor">{{ item.nomor }}</div>
          </div>
          <div class="kanan">
            <div class="arab">{{ item.ar }}</div>
            <div class="indo" v-if="showId == true">{{ item.id }}</div>
            <div class="translate" v-html="item.tr"></div>
          </div>
        </div>
      </div>

      <div class="audio floating">
        <audio controls>
          <source :src="selectedSurah.audio" type="audio/mp3" />

          Your browser does not support the audio element.
        </audio>
      </div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src

import allsurah from "../json/allData.json";

export default {
  name: "Home",
  data() {
    return {
      showId: false,
      keyword: "",
      allSurah: allsurah,
      selectedSurah: null,
    };
  },

  computed: {
    filteredSurah() {
      return this.keyword
        ? this.allSurah.filter((item) =>
            item.nama.toLowerCase().includes(this.keyword.toLowerCase())
          )
        : this.allSurah;
    },
  },
  methods: {
    resetSurah() {
      this.selectedSurah = null;
      this.$router.replace({
        name: "Home",
        params: {
          surahname: null,
          surahnumber: null,
          ayatnumber: null,
        },
      });
    },
    slugTthis(title) {
      var slug = "";
      // Change to lower case
      var titleLower = title.toLowerCase();
      // Letter "e"
      slug = titleLower.replace(/e|é|è|ẽ|ẻ|ẹ|ê|ế|ề|ễ|ể|ệ/gi, "e");
      // Letter "a"
      slug = slug.replace(/a|á|à|ã|ả|ạ|ă|ắ|ằ|ẵ|ẳ|ặ|â|ấ|ầ|ẫ|ẩ|ậ/gi, "a");
      // Letter "o"
      slug = slug.replace(/o|ó|ò|õ|ỏ|ọ|ô|ố|ồ|ỗ|ổ|ộ|ơ|ớ|ờ|ỡ|ở|ợ/gi, "o");
      // Letter "u"
      slug = slug.replace(/u|ú|ù|ũ|ủ|ụ|ư|ứ|ừ|ữ|ử|ự/gi, "u");
      // Letter "d"
      slug = slug.replace(/đ/gi, "d");
      // Trim the last whitespace
      slug = slug.replace(/\s*$/g, "");
      // Change whitespace to "-"
      slug = slug.replace(/\s+/g, "-");

      return slug;
    },
    gotoSurah(item) {
      this.$router.replace({
        name: "Home",
        params: {
          surahname: this.slugTthis(item.nama),
          surahnumber: item.nomor,
          ayatnumber: "0",
        },
      });
      this.selectedSurah = item;
    },
  },
};
</script>
